
import { defineComponent, PropType, computed, ref } from 'vue'
import { InverterInfo } from '../store'
import { emitter } from '@/views/Monitor.vue'

export default defineComponent({
  name: 'MonitorInverterList',
  props: {
    list: {
      type: Array as PropType<InverterInfo[]>,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    totalCount: {
      type: Number,
      required: true
    }
  },
  setup(props) {
    const columnList = computed(() => {
      console.log('list', props.list)
      return props.list
    })
    const hideOnSinglePage = true

    const currentPage = ref(1)
    const pageSize = 5
    const handleCurrentChange = () => {
      console.log('handleCurrentChange', currentPage.value)
      emitter.emit('handleCurrentChange', { currentPage: currentPage.value })
    }
    return {
      columnList,
      hideOnSinglePage,
      handleCurrentChange,
      currentPage,
      pageSize
    }
  }
})
